.table {
    width: 400px;
    margin: auto;
    margin-bottom: 10px;
    border: 3px solid brown;
    border-radius: 10px;
    box-shadow: 0 5px 10px rgb(90, 22, 22);
}

.table thead th {
    font-weight: 300;
    text-align: left;
    border: none;
    padding: 10px 10px;
    font-size: 14px;
    border-top: 3px solid beige;
    text-align: center;
}

.table tbody td {
    text-align: center;
    border: none;
    padding: 10px 10px;
    font-size: 14px;
}